import React from 'react';
import { connect } from 'react-redux';
import {  } from '../actions';

export function Stepper (props) {

  const Dot = props => <div {...styles.dot(props.active)}></div>;
  const Dots = (count, pageIndex) => Array(count).fill(null).map( (dot, i) => <Dot key={`${pageIndex}-${i}`} active={pageIndex === i}/> );

  const Sections = (pageIndexes, sectionIndex) => {
    return (
      <React.Fragment>
        <section {...styles.section}><Dot active={props.sectionIndex === 0}></Dot></section>
        <section {...styles.section}>{Dots(props.reviewables.length, sectionIndex === 1 ? pageIndexes[1] : null)}</section>
        <section {...styles.section}>{Dots(props.areas.length, sectionIndex === 2 ? pageIndexes[2] : null)}</section>
      </React.Fragment>
    )
  }

  return (
    <div {...styles.container}>
      {Sections(props.pageIndexes, props.sectionIndex)}
    </div>
  );
};

const styles = {
  dot: isActive => ({
    style: {
      width: '5px',
      height: '5px',
      margin: '4px 2px',
      borderRadius: '50%',
      backgroundColor: 'rgba(0,0,0,0.42)',
      ...( isActive && {
        backgroundColor: 'rgba(0,0,0,0.87)',
      })
    }
  }),
  section: {
    style: {
      display: 'flex',
      flexDirection: 'row'
    }
  },
  container: {
    style: {
      width: '100%',
      position: 'absolute',
      bottom: '2px',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center'
    }
  }
}

const mapStateToProps = state => ({
  pageIndexes: state.pageIndexes,
  sectionIndex: state.sectionIndex,
  doables: state.doables,
  reviewables:  state.doables.filter( doable => doable.area === ''),
  areas: [...new Set(state.doables.map( doable => doable.area ).filter( area => area !== '' ))]
});

const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(Stepper);
